import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			// 用户昵称
			userName: ''
		}
	},
	methods: {
		// 打开弹窗
		open() {
			this.$refs.popup.open()
			// uni.navigateTo({
			// 	url: '/pages/login/login'
			// });
		},

		// 关闭弹窗
		close() {
			this.$refs.popup.close()
		},
		// 去我的足迹页面
		goFootprint() {
			uni.navigateTo({
				url: '/pages/footprint/footprint'
			});
		},
		// 去我的行程页面
		goTrip() {
			uni.navigateTo({
				url: '/pages/trip/trip'
			});
		},
		// 去我的订单页面
		goOrderList() {
			uni.navigateTo({
				url: '/pages/orderList/orderList'
			});
		},
		// 确定退出
		signOut() {
			uni.showModal({
				content: '是否退出登录',
				confirmColor: '#59b9a6',
				success: (res) => {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		},
		// 去设置页面
		goSetUp() {
			uni.navigateTo({
				url: '/pages/setUp/setUp'
			});
		},
		// 登录
		goLogin() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					console.log(res)
					this.getUser()
				}
			});
		},
		// 获取用户信息
		getUser() {
			uni.getUserInfo({
				provider: 'weixin',
				success: (res) => {
					console.log(res)
					if (res.errMsg == 'getUserInfo:ok') {
						uni.navigateTo({
							url: '/pages/login/login'
						});
					}
				}
			});
		}
	},
	computed: {
		isUserName() {
			if (this.userName == '') {
				return true
			}
			return false
		}
	},
}
